<template>
  <div class="player-nfts-page">
    <div class="container">
      <div v-if="loading" class="loading-state">
        <div class="spinner"></div>
        <p>Loading user information...</p>
      </div>
      
      <div v-else-if="error" class="error-state">
        <p>{{ error }}</p>
        <router-link to="/" class="back-button">Back to Home</router-link>
      </div>
      
      <template v-else>
        <div class="user-profile">
          <div class="user-avatar">
            <span class="avatar-placeholder">{{ userInitial }}</span>
          </div>
          <div class="user-info">
            <h1><router-link :to="`/player/${userTag}`">{{ userTag }}</router-link></h1>
            <div class="user-stats">
              <div class="stat-item">
                <span class="stat-value">{{ totalNfts }}</span>
                <span class="stat-label">NFTs</span>
              </div>
            </div>
          </div>
        </div>
        
        <UserPublicNftsList :userTag="userTag" />
      </template>
    </div>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue';
import { useRoute } from 'vue-router';
import axios from 'axios';
import UserPublicNftsList from '@/components/pages/nft-bitcat/UserPublicNftsList.vue';

export default {
  name: 'PlayerNftsPage',
  components: {
    UserPublicNftsList
  },
  
  setup() {
    const route = useRoute();
    const userTag = ref('');
    const loading = ref(true);
    const error = ref(null);
    const totalNfts = ref(0);
    
    const userInitial = computed(() => {
      if (userTag.value) {
        return userTag.value.charAt(0).toUpperCase();
      }
      return '';
    });
    
    const fetchUserInfo = async () => {
      loading.value = true;
      error.value = null;
      
      try {
        // Get userTag from route params
        userTag.value = route.params.userTag;
        
        // Check if user exists and get their NFT count
        const response = await axios.get(`/nft-bitcat/user/${userTag.value}/nfts`, {
          params: { page: 1, limit: 1 }
        });
        
        if (response.data.success) {
          totalNfts.value = response.data.pagination.total;
        } else {
          error.value = 'Failed to load user information';
        }
      } catch (err) {
        console.error('Error fetching user info:', err);
        error.value = err.response?.data?.message || 'User not found or an error occurred';
      } finally {
        loading.value = false;
      }
    };
    
    onMounted(() => {
      fetchUserInfo();
    });
    
    return {
      userTag,
      loading,
      error,
      totalNfts,
      userInitial
    };
  }
};
</script>

