<template>
  <div class="user-public-nfts">
    <div class="section-header">
      <h2>{{ userTag }}'s NFT Collection</h2>
      <p v-if="loading">Loading NFTs...</p>
      <p v-else-if="error">{{ error }}</p>
      <p v-else-if="nfts.length === 0">This user hasn't minted any NFTs yet.</p>
    </div>
    
    <div v-if="!loading && !error && nfts.length > 0" class="nfts-grid">
      <div v-for="nft in nfts" :key="nft.assetPublicKey" class="nft-card">
        <div class="nft-image-container">
          <img :src="nft.imageUri" :alt="nft.name" class="nft-image">
        </div>
        <div class="nft-info">
          <h3>{{ nft.name }}</h3>
          <p class="nft-date">Minted on {{ formatDate(nft.createdAt) }}</p>
          <div class="nft-actions">
            <router-link :to="`/nft/${nft.assetPublicKey}`" class="view-button">
              View Details
            </router-link>
          </div>
        </div>
      </div>
    </div>
    
    <!-- Pagination -->
    <div v-if="pagination.totalPages > 1" class="pagination">
      <button 
        @click="changePage(1)" 
        :disabled="pagination.page === 1"
        class="pagination-button"
      >
        &laquo;
      </button>
      
      <button 
        @click="changePage(pagination.page - 1)" 
        :disabled="pagination.page === 1"
        class="pagination-button"
      >
        &lsaquo;
      </button>
      
      <div class="pagination-info">
        Page {{ pagination.page }} of {{ pagination.totalPages }}
      </div>
      
      <button 
        @click="changePage(pagination.page + 1)" 
        :disabled="pagination.page === pagination.totalPages"
        class="pagination-button"
      >
        &rsaquo;
      </button>
      
      <button 
        @click="changePage(pagination.totalPages)" 
        :disabled="pagination.page === pagination.totalPages"
        class="pagination-button"
      >
        &raquo;
      </button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import { ref, onMounted, watch } from 'vue';
import { useRoute, useRouter } from 'vue-router';

export default {
  name: 'UserPublicNftsList',
  props: {
    userTag: {
      type: String,
      required: true
    }
  },
  
  setup(props) {
    const route = useRoute();
    const router = useRouter();
    
    const nfts = ref([]);
    const loading = ref(true);
    const error = ref(null);
    const pagination = ref({
      total: 0,
      page: 1,
      limit: 10,
      totalPages: 0
    });
    
    const fetchUserNfts = async (page = 1) => {
      loading.value = true;
      error.value = null;
      
      try {
        const response = await axios.get(`/nft-bitcat/user/${props.userTag}/nfts`, {
          params: {
            page,
            limit: pagination.value.limit
          }
        });
        
        if (response.data.success) {
          nfts.value = response.data.nfts || [];
          pagination.value = response.data.pagination;
        } else {
          error.value = response.data.message || `Failed to load ${props.userTag}'s NFTs`;
        }
      } catch (err) {
        console.error('Error fetching user NFTs:', err);
        error.value = err.response?.data?.message || 'An error occurred while fetching NFTs';
      } finally {
        loading.value = false;
      }
    };
    
    const changePage = (newPage) => {
      if (newPage < 1 || newPage > pagination.value.totalPages) {
        return;
      }
      
      // Update URL with new page parameter
      router.push({
        path: route.path,
        query: { ...route.query, page: newPage }
      });
      
      // Fetch data for the new page
      fetchUserNfts(newPage);
    };
    
    const formatDate = (dateString) => {
      const date = new Date(dateString);
      return date.toLocaleDateString('en-US', {
        year: 'numeric',
        month: 'short',
        day: 'numeric'
      });
    };
    
    onMounted(() => {
      // Get page from URL query parameter
      const page = parseInt(route.query.page) || 1;
      pagination.value.page = page;
      fetchUserNfts(page);
    });
    
    // Watch for changes in userTag prop
    watch(() => props.userTag, (newUserTag) => {
      if (newUserTag) {
        pagination.value.page = 1;
        fetchUserNfts(1);
      }
    });
    
    return {
      nfts,
      loading,
      error,
      pagination,
      changePage,
      formatDate
    };
  }
};
</script>

